<script>
export default {
  props: {
    path: {
      type: String,
      default: () => '/'
    },
    activeColor: {
      type: String,
      default: () => 'red'
    }
  },
  computed: {
    isActive () {
      return this.$route.path.includes(this.path)
    }
  },
  data () {
    return {

    }
  },
  methods: {
    goToPage () {
      this.$router.replace(this.path)
    }
  }

}
