import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '@/views/home'
import category from '@/views/category'
import cart from '@/views/cart'
import profile from '@/views/profile'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'home',
    component: home

  },
  {
    path: '/category',
    name: 'category',
    component: category
  },
  {
    path: '/cart',
    name: 'cart',
    component: cart
  },
  {
    path: '/profile',
    name: 'profile',
    component: profile
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router